/**
 * Kullanm:
 *	@include ReSassResponsive(240){}
 *  ...
 *  ...
 *  ...
 *  @include ReSassResponsive(1500){}
 *
 */
@mixin ReSassResponsive( $point ) {
	@if $point==240 {
		@media ( max-width: 240px ) {
			@content;
		}
	}
	@if $point==320 {
		@media ( max-width: 320px ) {
			@content;
		}
	}
	@if $point==480 {
		@media ( max-width: 480px ) {
			@content;
		}
	}
	@if $point==600 {
		@media ( max-width: 600px ) {
			@content;
		}
	}
	@if $point==768 {
		@media ( max-width: 768px ) {
			@content;
		}
	}
	@if $point==992 {
		@media ( max-width: 992px ) {
			@content;
		}
	}
	@if $point==1024 {
		@media ( max-width: 1024px ) {
			@content;
		}
	}
	@if $point==1140 {
		@media ( max-width: 1140px ) {
			@content;
		}
	}
	@if $point==1200 {
		@media ( max-width: 1199px ) {
			@content;
		}
	}
	@if $point==1280 {
		@media ( max-width: 1280px ) {
			@content;
		}
	}
	@if $point==1400 {
		@media ( max-width: 1400px ) {
			@content;
		}
	}
	@if $point==1500 {
		@media ( max-width: 1500px ) {
			@content;
		}
	}
}