$mainpageHeaderImgPath : "../../img/mainpage";
$globalSvgIconImgPath : "../../img/global/svg";

.header{
  $headerclassname: &;

  &-top{
    padding: 0;
    height: 40px;
    border-bottom: 1px solid transparentize($global-clr-soft-gray, 0.7);
    @include transition(all, 0.1s, ease-out);

    @include ReSassResponsive(1400){
      height: 36px;
    }

    > div[class*="col"]{
      padding-left: 0;
      padding-right: 0;
    }

    &-language{
      text-align: center;
      margin: 0;

      a{color: $global-clr-white;}

      &-item{
        position: relative;
        padding: 0 10px;
        display: inline-block;
        color: $global-clr-soft-gray;
        font-size: $global-font-size;

        @include ReSassResponsive(1400){
          font-size: 13px;
          padding: 0 6px;
        }

        &::before{
          content: "";
          position: absolute;
          width: 1px;
          height: 10px;
          top: calc(50% - 5px);
          right: -3px;
          background-color: transparentize($global-clr-soft-gray, 0.7);
        }
        &:first-child{
          padding-left: 0;
        }
        &:last-child{
          padding-right: 0;
          &::before{display: none;}
        }

      }
      &-selection{
        &.active{color: $global-clr-soft-gray; opacity: 0.75;}
      }
    }

    &-city{
      margin: 0;
      text-align: center;

      a{color: $global-clr-white;}

      &-item{
        position: relative;
        padding: 0 8px;
        opacity: 0.75;
        //display: inline-block;
        display: inline-flex;
        color: $global-clr-soft-gray;
        font-size: $global-font-size;

        @include ReSassResponsive(1400){
          font-size: 13px;
        }

        &::before{
          content: "";
          position: absolute;
          width: 3px;
          height: 3px;
          top: calc(50% - 1px);
          right: 0;
          background-color: transparentize($global-clr-soft-gray, 0.7);
          @include border-radius(50%)
        }
        &:first-child{
          padding-left: 0;
        }
        &:last-child{
          padding-right: 0;
          &::before{display: none;}
        }
      }
      &-name,
      &-clock{
        display: inline-block;
        margin: 0 2px;
      }
      &-clock{
        min-width: 45px;
        text-align: left;

        @include ReSassResponsive(1400){
          min-width: 36px;
        }
      }
    }

    &-singin{}

    &-userarea{

      #{$headerclassname}-userarea-user{

        &-name{
          font-size: 15px;

          @include ReSassResponsive(1400){
            font-size: 13px;
          }
        }

        .dropdown-menu{
          padding-top: 8px;
        }
      }
    }
  }

}